:root,
[data-theme=light] {
  --xxl-width: ;
  --xl-width: 1200px;
  --lg-width: 992px;
  --md-width: 768px;
  --sm-width: 576px;
  --xs-width: 0;
  --appreciation-color: var(--green-600);
  --appreciation-bg: var(--green-100);
  --criticism-color: var(--red-600);
  --criticism-bg: var(--red-100);
  --error-bg: var(--red-50);
  --error-border: var(--red-400);
  --page-head-height: 60px;
  --page-bottom-margin: 60px;
  --checkbox-right-margin: var(--margin-xs);
  --checkbox-size: 14px;
  --checkbox-focus-shadow: var(--focus-default);
  --date-active-text: var(--gray-100);
  --date-active-bg: var(--gray-900);
  --date-range-bg: var(--subtle-fg);
  --timeline-item-icon-size: 30px;
  --timeline-item-left-margin: var(--margin-lg);
  --timeline-item-bottom-margin: var(--margin-sm);
  --timeline-content-max-width: 700px;
  --timeline-left-padding: calc(var(--padding-xl) + var(--timeline-item-icon-size) / 2);
  --timeline-badge-color: var(--gray-900);
  --timeline-badge-bg: var(--gray-100);
  --user-mention-bg-color: var(--fg-color);
  --group-mention-bg-color: var(--bg-purple);
  --skeleton-bg: var(--gray-100);
  --progress-bar-bg: var(--primary);
}

input[type=checkbox] {
  position: relative;
  width: var(--checkbox-size) !important;
  height: var(--checkbox-size);
  margin-right: var(--checkbox-right-margin) !important;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid var(--gray-500);
  box-sizing: border-box;
  border-radius: 4px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  color-adjust: exact;
}
.grid-static-col input[type=checkbox] {
  margin-right: 0 !important;
}
input[type=checkbox]:checked {
  background-color: var(--primary);
  background-image: url("data:image/svg+xml, <svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 4.00001L2.66667 5.80001L7 1.20001' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/></svg>"), var(--checkbox-gradient);
  background-size: 57%, 100%;
  box-shadow: none;
  border: none;
}
input[type=checkbox]:focus {
  outline: none;
  box-shadow: var(--checkbox-focus-shadow);
}
input[type=checkbox].disabled-deselected, input[type=checkbox]:disabled {
  background-color: var(--disabled-control-bg);
  box-shadow: inset 0px 1px 7px rgba(0, 0, 0, 0.1);
  border: 0.5px solid var(--dark-border-color);
  pointer-events: none;
}
input[type=checkbox].disabled-selected, input[type=checkbox]:disabled:checked {
  background-color: var(--gray-600);
  background-image: url("data:image/svg+xml, <svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 4.00001L2.66667 5.80001L7 1.20001' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/></svg>");
  background-size: 57%;
  box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.1);
  border: none;
  pointer-events: none;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 16px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--switch-bg);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  left: 2px;
  bottom: 2px;
  background-color: var(--neutral);
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--invert-neutral);
}

input:focus + .slider {
  box-shadow: var(--focus-default);
}

input:checked + .slider:before {
  transform: translateX(10px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

label.switch {
  margin-bottom: 0;
}

.slider.round:before {
  border-radius: 50%;
}

@media print {
  svg[data-barcode-value] > rect {
    fill: white !important;
  }
  svg[data-barcode-value] > g {
    fill: black !important;
  }
  .print-hide {
    display: none !important;
  }
  .overflow-wrap-anywhere * {
    word-break: break-all;
  }
}
.action-banner {
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  font-size: var(--text-md);
}

.invalid-state {
  display: grid;
  place-content: center;
  height: 100vh;
}
.invalid-state img {
  margin: auto;
}

.overflow-wrap-anywhere * {
  overflow-wrap: anywhere;
}